
import { defineComponent } from "vue";

export default defineComponent({
  name: "CopyToClipboard",
  props: {
    text: {
      type: String,
      required: true
    }
  },
  methods: {
    async copy() {
      try {
        await navigator.clipboard.writeText(this.text);
      } catch (e) {
        console.log(e);
      }
    }
  }
});
